.dialogs{
    display: grid;
    grid-template-columns: 2fr 10fr;
}
.dialogsItems{
    padding: 10px;
}
.dialogsItems .dialog a{
    text-decoration: none;
}
.dialog img {
    max-width: 30px;
    height: 30px;
    border-radius: 50%;
    position: absolute;
    left: -40px;
    top: -5px;
    z-index: 1;
  }
.dialog{
    margin-bottom: 20px;
    margin-left: 40px;
    position: relative;
}
.dialog:last-child{
    margin-bottom: 0px;
}
.dialogsItems .active{
    color:  gold;
}

.messages{
    padding: 10px;
}
.messages .message{
    margin-bottom: 20px;
}